import { createStore } from "vuex";
import router from "@/router";

import account from "./modules/account";
import {
  COMMON_ADD_BREADCRUMB,
  COMMON_CLEAR_BREADCRUMB, COMMON_LOGOUT_TIMER_START,
  COMMON_SET_LOADING,
  COMMON_SET_SERVER_ERRORS, COMMON_UPDATE_LOGOUT_TIMEOUT
} from "./actions/common";
import { ACCOUNT_REMOVE_TOKEN } from "./actions/account";
import moment from "moment";


export default createStore({
  state: {
    loading: false,
    serverErrors: [],
    breadcrumb: [],
    logoutTimout: (new Date()).getTime() + 600000,
    logoutTimer: null,
    logoutTime: "00:00"
  },
  mutations: {
    [COMMON_SET_LOADING]: (state, value) => {
      state.loading = !!value
    },
    [COMMON_SET_SERVER_ERRORS]: (state, value) => {
      state.serverErrors = value
    },
    [COMMON_ADD_BREADCRUMB]: (state, item) => {
      state.breadcrumb.push(item)
    },
    [COMMON_CLEAR_BREADCRUMB]: (state) => {
      state.breadcrumb.splice(0, state.breadcrumb.length)
    },
    [COMMON_UPDATE_LOGOUT_TIMEOUT]: (state) => {
      state.logoutTimout = (new Date()).getTime() + 600000
      state.logoutTime = moment.utc(600000).format("mm:ss")
    }
  },
  actions: {
    [COMMON_LOGOUT_TIMER_START]: ({state, commit}) => {
      state.logoutTimer = setInterval(() => {
        let diff = state.logoutTimout - (new Date()).getTime();
        state.logoutTime = moment.utc(diff).format("mm:ss")

        if (diff <= 0) {
          commit(ACCOUNT_REMOVE_TOKEN)
          router.push({name: "account.login"})
          clearInterval(state.logoutTimer)
          state.logoutTimer = null
        }
      }, 500)
    }
  },
  modules: {
    account
  },
});
