import {
  ACCOUNT_REMOVE_TOKEN,
  ACCOUNT_SET_PROFILE,
  ACCOUNT_SET_TOKEN,
  ACCOUNT_UPDATE_PROFILE
} from "../actions/account";
import axios from "axios";

const JWT_TOKEN_KEY = "jwt-token";
const JWT_REFRESH_TOKEN_KEY = "jwt-refresh-token";

const state = {
  profile: null,
  accessToken: localStorage.getItem(JWT_TOKEN_KEY),
  refreshToken: localStorage.getItem(JWT_REFRESH_TOKEN_KEY)
}

// let XMLHttpRequestOpen = XMLHttpRequest.prototype.open;
// let XMLHttpRequestOpenAuthorization = function(){
//   let res = XMLHttpRequestOpen.apply(this, arguments);
//   this.setRequestHeader('Authorization', `Bearer ${state.accessToken}`);
//   return res;
// }

if (state.accessToken) {
  axios.defaults.headers.common['Authorization'] = `Bearer ${state.accessToken}`
  // XMLHttpRequest.prototype.open = XMLHttpRequestOpenAuthorization
}

const getters = {
  isAuthenticated: state => !!state.profile,
  accountFullName: (state) => {
    if (!state.profile) {
      return "-";
    }

    if (!state.profile.last_name && !state.profile.first_name) {
      return state.profile.username;
    }

    return `${state.profile.last_name} ${state.profile.first_name}` //(${state.profile.username});
  }
}

const mutations = {
  // profileni set qiladi
  [ACCOUNT_SET_PROFILE]: (state, profile) => {
    state.profile = profile
  },
  [ACCOUNT_UPDATE_PROFILE]: (state, form) => {
    Object.keys(form).forEach(key => {
      state.profile[key] = form[key];
    })
  },
  // Logoutga javob beradi
  [ACCOUNT_REMOVE_TOKEN]: (state) => {
    state.profile = null
    state.accessToken = null
    state.refreshToken = null

    // XMLHttpRequest.prototype.open = XMLHttpRequestOpen
    axios.defaults.headers.common['Authorization'] = null
    localStorage.removeItem(JWT_TOKEN_KEY)
    localStorage.removeItem(JWT_REFRESH_TOKEN_KEY)
  },
  // tokenlar qiymatini o'zgartiradi
  [ACCOUNT_SET_TOKEN]: (state, data) => {
    state.accessToken = data.token
    state.refreshToken = data.refresh

    // XMLHttpRequest.prototype.open = XMLHttpRequestOpenAuthorization
    axios.defaults.headers.common['Authorization'] = `Bearer ${data.token}`
    localStorage.setItem(JWT_TOKEN_KEY, state.accessToken)
    localStorage.setItem(JWT_REFRESH_TOKEN_KEY, state.refreshToken)
  }
}

export default {
  state,
  getters,
  mutations
}