import { createRouter, createWebHistory } from "vue-router";
import Dashboard from "@/views/dashboard/Dashboard.vue";
import account from "./account";
import calendar from "./calendar";
import event from "./event";
import filecloud from "./filecloud";
import info from "./info";
import task from "./task";

import store from "@/store";

import { COMMON_CLEAR_BREADCRUMB } from "../store/actions/common";

const routes = [
  {
    path: "/",
    name: "dashboard",
    component: Dashboard
  },
  {
    path: "/account",
    component: () => import("@/views/account/Index.vue"),
    children: account
  },
  {
    path: "/calendar",
    component: () => import("@/views/calendar/Index.vue"),
    children: calendar
  },
  {
    path: "/event",
    component: () => import("@/views/event/Index.vue"),
    children: event
  },
  {
    path: "/filecloud",
    component: () => import("@/views/filecloud/Index.vue"),
    children: filecloud
  },
  {
    path: "/info",
    component: () => import("@/views/info/Index.vue"),
    children: info
  },
  {
    path: "/task",
    component: () => import("@/views/task/Index.vue"),
    children: task
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach(function(to, from, next) {
  store.commit(COMMON_CLEAR_BREADCRUMB)

  document.title = window.vueapp.config.globalProperties.$t(to.name);
  let toLogin = to.name === 'account.login';
  let fromLogin = from.name === 'account.login';
  if (!store.getters.isAuthenticated) {
    if (fromLogin) {
      return;
    }

    if (!toLogin) {
      next({ name: "account.login" });
      return;
    }

    next();
    return
  }

  if (toLogin) {
    next({name: "dashboard"});
    return;
  }

  next();
})


export default router;