<script>
import {ACCOUNT_REMOVE_TOKEN} from "../store/actions/account";

export default {
  props: {
    infopage: {
      default: false,
      type: Boolean
    }
  },
  data() {
    return {
      visibleFull: false,
      items: [
        {}
      ],
      values: [
        {
          label: this.$t('menu.document'),
          icon: 'pi pi-fw pi-th-large'
        },
        {
          label: this.$t('filecloud.list'),
          icon: 'pi pi-fw pi-cloud',
          url: 'filecloud.list',
          command: () => {
            this.$router.push({name: 'filecloud.list'});
          }
        },
        {
          label: this.$t('menu.mail'),
          icon: 'pi pi-fw pi-envelope'
        },
        {
          label: this.$t('calendar.list'),
          icon: 'pi pi-fw pi-calendar',
          url: 'calendar.list',
          command: () => {
            this.$router.push({name: 'calendar.list'});
          }
        },
        {
          label: this.$t('info.list'),
          icon: 'pi pi-fw pi-info-circle',
          url: 'info.list',
          command: () => {
            this.$router.push({name: 'info.list'});
          }
        },
        {
          label: this.$t('menu.statistics'),
          icon: 'pi pi-fw pi-chart-bar'
        }
      ],
      bellItems: [
        {
          label: 'Notification 1',
          items: [
            {
              label: 'Notification text'
            }
          ]
        },
        {
          label: 'Notification 2',
          items: [
            {
              label: 'Notification text'
            }
          ]
        }
      ],
      envelopeItems: [
        {label: 'Message 2'},
        {label: 'Message 3'},
        {label: 'Message 4'},
      ]
    }
  },
  methods: {
    bellPopup(event) {
      this.$refs.bellPopup.toggle(event)
    },
    envelopePopup(event) {
      this.$refs.envelopePopup.toggle(event)
    },
    toggleAccountMenu(event) {
      this.$refs.accountMenu.toggle(event)
    },
    showLogoutConfirm() {
      this.$confirm.require({
        message: this.$t('account.logout-confirm'),
        header: this.$t('account.logout'),
        acceptLabel: this.$t('yes'),
        rejectLabel: this.$t('no'),
        icon: 'pi pi-exclamation-triangle',
        acceptClass: 'p-button-danger',
        accept: () => {
          this.$toast.add({
            severity: 'success',
            summary: this.$t('account.logout'),
            detail: this.$t('account.logout-success'),
            life: 3000
          });

          this.$store.commit(ACCOUNT_REMOVE_TOKEN)
          this.$router.push({name: 'account.login'})
        },
        reject: () => {

        }
      });
    },
  },
  computed: {
    accountItems() {
      return [
        {
          label: this.$store.getters.accountFullName,
          items: [
            {
              label: this.$t('account.profile'),
              icon: 'pi pi-user',
              command: () => {
                this.$router.push({name: 'account.profile'});
              }
            },
            {
              label: this.$t('account.logout'),
              icon: 'pi pi-sign-out',
              command: this.showLogoutConfirm
            }
          ]
        }
      ]
    }
  }
}
</script>


