"use strict";
import {helpers} from "@vuelidate/validators";
import store from "@/store";
import { COMMON_ADD_BREADCRUMB } from "../store/actions/common";

function setMessages(t, obj) {
    Object.keys(obj).forEach(key => {
        Object.keys(obj[key]).forEach(k => {
            let msg = t('validation.' + k)

            Object.keys(obj[key][k].$params).forEach(kk => {
                msg = msg.replace('[' + k + ']', obj[key][k].$params[kk]);
            });

            obj[key][k] = helpers.withMessage(msg, obj[key][k]);
        })
    })
}

function breadcrumb(label) {
    store.commit(COMMON_ADD_BREADCRUMB, {label: label})
}

export default {
    install: (app) => {
        app.config.globalProperties.$setMessages = function(obj) {
            setMessages(this.$t, obj);

            return obj;
        };

        app.config.globalProperties.$breadcrump = breadcrumb;
    }
};