import './plugins/axios'
import {createApp} from "vue";
import App from "./App.vue";
import PrimeVue from 'primevue/config';
import i18n from "./plugins/i18n";
import router from "./router";
import store from "./store";
import helpers from "@/plugins/helpers";
import "@/assets/styles.scss";
import primevue from "./plugins/primevue";
import api from "./plugins/api"
import {$api} from '@/plugins/api'
import ConfirmationService from 'primevue/confirmationservice';
import ToastService from 'primevue/toastservice';
import {ACCOUNT_SET_PROFILE} from "./store/actions/account";
import { COMMON_LOGOUT_TIMER_START } from "./store/actions/common";
// import {ACCOUNT_SET_PROFILE, SET_TOKEN} from "./store/actions/account";
import BadgeDirective from 'primevue/badgedirective';


function init_vue() {
    let app = createApp(App)
        .use(store)
        .use(router)
        .use(i18n)
        .use(PrimeVue)
        .use(api)
        .use(ConfirmationService)
        .use(ToastService)
        .use(helpers);
    app.directive('badge', BadgeDirective);

    Object.keys(primevue).forEach(item => {
        app.component(item, primevue[item]);
    })
    app.mount("#app");

    window.vueapp = app
}

async function init() {
    let resp = await $api.account.me.get()
    if (resp.success) {
        store.commit(ACCOUNT_SET_PROFILE, resp.data.user)
        store.dispatch(COMMON_LOGOUT_TIMER_START)
    }

    init_vue();
}

init();
